.block-iccwc-hero-banner {
  height: 400px;
  margin-bottom: 5px;
}
.block-iccwc-hero-banner .block-content {
  height: 100%;
}
.block-iccwc-hero-banner .field--name-field-banner-image {
  position: absolute;
  left: 0;
  top: 0;
  z-index: -1;
  margin-left: 50%;
  transform: translateX(-50%);
}
.block-iccwc-hero-banner img {
  max-width: none;
}
.block-iccwc-hero-banner .tags {
  display: flex;
}
.block-iccwc-hero-banner h1 {
  font-weight: bold;
  font-family: "Source Sans Pro", sans-serif;
  color: var(--color-text);
  font-size: 44px;
  line-height: 50px;
  color: #fff;
  text-shadow: 0px 0px 10px rgba(0, 0, 0, 0.35);
}
@media (max-width: 991px) {
  .block-iccwc-hero-banner h1 {
    font-size: 24px;
    line-height: 26px;
  }
}

.hero-block {
  display: flex;
  flex-direction: column;
  color: #fff;
  height: 100%;
  background: linear-gradient(360deg, rgba(35, 31, 32, 0.6) 29.09%, rgba(35, 31, 32, 0) 93.02%);
  border-top: 4px solid var(--color-orange);
  position: relative;
  box-sizing: content-box;
}
.hero-block:after {
  content: "";
  background: var(--color-orange);
  z-index: -2;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}
.hero-block .title-subtitle {
  position: relative;
}
.hero-block .title-subtitle .button.js-form-submit.form-submit.btn.btn-primary {
  left: 260px;
  position: absolute;
}
.hero-block .title-subtitle .form-text {
  margin-top: 0;
}
.hero-block .iccwc-search-form input[type=text] {
  padding: 10px;
  width: 300px;
  height: 45px;
  border-radius: 20px;
  justify-content: space-between;
  gap: 85px;
}
.hero-block .caption {
  margin-left: auto;
  margin-top: auto;
  font-size: 12px;
  margin-bottom: 20px;
}
.hero-block .container {
  height: 100%;
  display: flex;
  flex-direction: column;
}
.hero-block .title-subtitle {
  margin-top: auto;
  margin-bottom: auto;
}
.hero-block h1 {
  margin-bottom: 20px;
}
.hero-block .summary {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  line-clamp: 3;
  -webkit-box-orient: vertical;
}
.hero-block .tag {
  text-transform: uppercase;
}
.hero-block .tag + .date:before {
  content: "|";
  margin: 0 5px;
}
.hero-block .left-bar {
  border-left: 4px solid #FFF;
  padding-left: 20px;
}

.breadcrumb {
  list-style-type: none;
  color: var(--color-gray-darker);
  background-color: var(--color-black);
  display: inline-flex;
  padding: 5px 10px;
}
.breadcrumb .breadcrumb-item {
  max-width: 300px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.breadcrumb .breadcrumb-item + .breadcrumb-item:before {
  content: "/";
  margin-left: 5px;
}
.breadcrumb a {
  color: var(--color-white);
}
.breadcrumb a:hover {
  color: var(--color-white);
}